.result-name-card {
    display: flex;
    justify-content: center;
    color: #e7e9f0;
    background: #282d4f;
    margin: 10px 15px;
    min-width: 150px;
    padding: 20px 20px;
    border-radius: 10px;
    box-shadow: 0 0 5px rgba(255, 255, 255, 0.1);
    transition: background 0.35s;
}

.result-name-card:hover {
    background: #4a5079;
    box-shadow: 0 0 5px rgba(red, green, blue, 0.2);
}

.result-name {
    margin: 0;
    font-weight: 300;
    font-size: 20px;
}

.card-link {
    text-decoration: none;
}