.head-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 60px;
}

.head-image {
    width: auto;
    transition: max-width 1s, min-width 1s;

}

.head-image-expanded {
    min-width: 300px;
    max-width: 330px;
}

.head-image-contracted{
    min-width: 150px;
    max-width: 180px;
}

.head-text {
    font-family: 'Hachi Maru Pop', cursive;
    margin: 15px 0 15px 0;
    transition: font-size 1s;
}

.head-text-expanded {
    font-size: 35px;
}

.head-text-contracted {
    font-size: 20px;
}